#home {
    color: var(--text);
    height: 100vh;
    text-decoration: none;
    font-weight: 400;
    font-size: 18px;
    overflow: scroll;
}

#home p {
    margin: 0;
}

nav {
    position: absolute;
    width: 100vw;
    box-sizing: border-box;
    top: 0;
    left: 0;
    background-color: var(--bg-trans);
    height: 80px;
    padding: 16px 80px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

h1, h2, h3 {
    margin: 0;
}

nav h1 {
    font-size: 2.5rem;
    font-weight: 600;
}

nav a {
    height: 100%;
}

#logo {
    height: 40px;
}

.spacer {
    flex: 1;
}

.pagelink {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 100%;
    padding: 0 32px;
    text-decoration: none;
    color: var(--text);
    background-color: transparent;
    transition: background-color 200ms;
    border-radius: 10px;
    font-size: 18px;
}

.pagelink:hover {
    background-color: rgba(255, 255, 255, 0.1);
}

#coralbg {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: -1;
}

#maincontent {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    overflow-x: hidden;
    text-align: center;
}

#maincontent .fullpage {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}

#maincontent .fullpagelook {
    height: calc(100vh - 112px);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
}

#maincontent h2 {
    font-weight: 600;
    font-size: 72px;
}

#maincontent p {
    font-size: 24px;
}

#maincontent button {
    padding: 16px 32px;
    margin: 32px;
    font-size: 24px;
    background-color: var(--highlight);
    transition: background-color 200ms;
    border-radius: 10px;
    font-weight: 600;
}

#maincontent button:hover {
    background-color: var(--primary);
}