#analyzer {
    background-color: var(--bg);
}

#analyzer button {
    margin: 0;
}

#analyzer h2 {
    font-size: 36px;
}

#analyzer .fullpagelook {

}

#analyzer_Ctr {
    gap: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

#analyzer_img {
    width: 50vh;
    height: 50vh;
}

#analyzerinfo {
    gap: 16px;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: flex-start;
    height: 100%;
}

.bad {
    color: orangered;
    font-size: 24px;
}

.info {
    font-size: 24px;
}