body {
  margin: 0;
  font-family: 'Work Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --bg: #00181e;
  --bg-trans: rgba(0, 24, 30, 0.8);
  --text: #eaeaea;
  --highlight: #d75a00;
  --primary: #009eb4;
}

button {
  color: var(--text);
  border: none;
}
